@mixin foundation-everything( $flex: true, $prototype: false) {
    @if $flex {
        $global-flexbox: true !global;
    }
    @include foundation-global-styles;
    @if not $flex {
        @include foundation-grid;
    }
    @else {
        @if $xy-grid {
            @include foundation-xy-grid-classes;
        }
        @else {
            @include foundation-flex-grid;
        }
    }
    @include foundation-typography;
    @include foundation-forms;
    @include foundation-button;
    @include foundation-accordion;
    @include foundation-accordion-menu;
    @include foundation-badge;
    @include foundation-breadcrumbs;
    @include foundation-button-group;
    @include foundation-callout;
    @include foundation-card;
    @include foundation-close-button;
    @include foundation-menu;
    @include foundation-menu-icon;
    @include foundation-drilldown-menu;
    @include foundation-dropdown;
    @include foundation-dropdown-menu;
    @include foundation-responsive-embed;
    @include foundation-label;
    @include foundation-media-object;
    @include foundation-off-canvas;
    @include foundation-orbit;
    @include foundation-pagination;
    @include foundation-progress-bar;
    @include foundation-slider;
    @include foundation-sticky;
    @include foundation-reveal;
    @include foundation-switch;
    @include foundation-table;
    @include foundation-tabs;
    @include foundation-thumbnail;
    @include foundation-title-bar;
    @include foundation-tooltip;
    @include foundation-top-bar;
    @include foundation-visibility-classes;
    @include foundation-float-classes;
    @if $flex {
        @include foundation-flex-classes;
    }
    @if $prototype {
        @include foundation-prototype-classes;
    }
}
/* MI Base
-----------------------------------------------------------------------------*/


body,
html {
  font-size: $bfs;
  hyphens: auto;
}




/* Headlines & Texts
-----------------------------------------------------------------------------*/

h1 {
  font-weight: bold;
  margin-bottom: $bfs-l;
  //font-size: rem-calc(25px);
}

h2 {
  margin-bottom: $bfs;
  //font-size: rem-calc(20px);
}

h3 {
  margin-bottom: $bfs;
  //font-size: rem-calc(20px);
}

h1,
h2,
h3,
h4 {
  line-height: 110% !important;
  hyphens: none;
}

p + h2, ul + h2{
  margin-top: $bfs-xxl;
}


@media screen and #{breakpoint(medium down)} {
  p {
    line-height: 130%;
  }
}





/* Lists
-----------------------------------------------------------------------------*/

li {
  line-height: 130%;
  margin-bottom: $bfs-xs;
}

li.accordion-item{
  margin-bottom: 0;
}


.is-small li{
  line-height: 110%;
  margin-bottom: $bfs-xxs;
}





/* Other Elements
-----------------------------------------------------------------------------*/

blockquote {
  border-left: solid $mi-border-small $mi-pink;
  padding: $bfs-xs $bfs;

  >p {
    margin-bottom: 0;
  }
}
/* MI Icons
-----------------------------------------------------------------------------*/

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/fonts/iconfont/MaterialIcons-Regular.eot); 
  src: local('Material Icons'),
    local('MaterialIcons-Regular'),
    url(/fonts/iconfont/MaterialIcons-Regular.woff2) format('woff2'),
    url(/fonts/iconfont/MaterialIcons-Regular.woff) format('woff'),
    url(/fonts/iconfont/MaterialIcons-Regular.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
/* MI Layout
-----------------------------------------------------------------------------*/

body{
  background-position: bottom right;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 50%;
}

@media screen and #{breakpoint(xxlarge)} {
  body{
    background-size: 40%;
  }
}

@media screen and #{breakpoint(medium)} {
  .grid-container.fluid.has-gap {
    margin-left: $from-left;
  }

  .off-canvas-wrapper {
    overflow: visible;
  }

  .off-canvas-content {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }

  header,
  nav,
  footer {
    flex-shrink: 0;
  }

  main {
    flex: 1 0 auto;
    /* Use 1 0 auto instead of just 1 for backwards compatibility with other browsers */
  }
}

.mi-stack{
  > * {
    margin-bottom: $bfs;
  }
}
/* MI Moduls
###############################################################################
-----------------------------------------------------------------------------*/

.m-mi-content-wrap{
	padding-bottom: $bfs;
	margin-bottom: $bfs;
}



/* MI Datenliste mit Trennlinien
-----------------------------------------------------------------------------*/
.mi-dl-overview {

  margin-bottom: $bfs-xl;

  > dt{
    display: block;
    float: left;
    min-width: 10px;
    padding: 0;
    font-weight: bold;
    color: #9313ce;
    padding-top: 0.4rem;
  }

  dd {
    border-bottom: solid 1px #b5b5b5;
    padding-top: 0.4rem;
  }

  dd:after{
    content: "";
    display: block;
    height: 1px;
    clear: both;
    margin: 0;
    padding: 0;
  }

  dt::after {
    content: ":";
    display: inline-block;
    padding-right: 0.3rem;
  }
}


/* MI Modulbeschreibung
-----------------------------------------------------------------------------*/
.mi-modulbeschreibung{
  h2{
    font-size: $bfs-l;
    margin-top: $bfs-xxl;
  }
}
/* MI Resets
-----------------------------------------------------------------------------*/

a.label:hover{
  cursor: pointer;
}

/* Keine Umrandung bei aktiven Links */
.menu li a:focus {
    box-shadow: none !important;
    outline: none;
}

// Accordion reset
// ---------
.accordion-item{
  border-bottom: solid 1px $mi-blau;
  margin-bottom: $bfs-xs !important;
  padding-bottom: $bfs-xs;

  &:last-child{
    border-bottom: none;
  }
}

.accordion-title{
  font-size: 1rem ;
  color: #000;
  border: none;
  padding-top: 0;
  padding-bottom: 0;

  &:focus, &[aria-expanded=true]{
    font-weight: bold;
    color: #000;
    background: none;
  }

  &:hover{
    background: none;
  }
}

.accordion-content{
  border: none;
  font-size: $bfs-s;
}

:last-child:not(.is-active)>.accordion-title {
  border-bottom: none;
  border-radius: 0 0 0 0;
}

.material-icons {
  vertical-align: middle;
}
/* MI State
-----------------------------------------------------------------------------*/
/* MI Typo
-----------------------------------------------------------------------------*/

/* Keine Umrandung bei aktiven Links */
.mi-linkliste {

  margin-left: 0;

  >li {
    display: block;
    margin-bottom: 0;
  }
}

.subheader {
  line-height: 100%;
  color: $mi-pink;
}

.lift-up {
  margin-top: -$bfs;
}

.invers {
  display: inline-block;
  padding: $bfs-xxs $bfs-xs;
  margin-top: $bfs;
  margin-bottom: $bfs;
  color: #fff;
  background: #000;
}

.invers.gruen {
  background: $mi-gruen;
}

.is-small{
  font-size: $bfs-s;
}

.mi-chapter-title{
  font-size: $bfs-xxl;

  &.is-colorful{
    color: $mi-lila;
  }
}
/* MI Utils
-----------------------------------------------------------------------------*/

.has-seperator{
  border-bottom: solid 1px $mi-gruen;
  margin-bottom: $bfs-xl;
  background-color: rgba(255,255,255,0.6);
  backdrop-filter: blur(20px);
}

.is-vertically-centered {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.arrow-up{

  margin-top: 5px;

  &::before {
    content: "";
    position: absolute;
    top: -13px;
    left: 36px;
    width: 0; 
    height: 0;
    border-left: 13px solid transparent;
    border-right: 13px solid transparent;
    border-bottom: 13px solid $mi-grau;
  }
}



